export default {
	data() {
		return {
			isactive:0,
			id:0,
			userinfo:[]
		}
	},
	onLoad(options) {
		this.id = options.id
	},
	methods: {
		select(id){
			
			let _self = this
			_self.isactive = id
			let reason = ''
			if(id == 1){
				reason = '临时有事改变行程，已与体验家协商'
			}else if(id==2){
				reason = '体验家时间不便，另行预约'
			}else if(id==3){
				reason = '预约了其他体验家'
			}else if(id==4){
				reason = '路途不便，找不到地方'
			}
			uni.request({
				url:_self.common.websiteUrl+"experhome_custormers_pinglun",
				header:{"user-token":"6a109faf305513d443337ddb1ad4cb9b"},
				method:"post",
				data:{
					'id':_self.id,
					'status':4,
					'reason':reason
					},
				success: (res) => {
					if(res.data.code==200){
						_self.common.callback('取消成功')
						setTimeout(()=>{
							uni.navigateTo({
							    url: '../waitreview/waitreview?id='+_self.id
							});
						},1000)
						 
					}else{
						_self.common.network()
					}
				},fail() {
					_self.common.network()
				}
			});
		}
	}
}
